/* global document, Office */

function getOwaOriginSafe() {
  const restUrl = Office.context?.mailbox?.restUrl;
  if (restUrl && /^https?:\/\//i.test(restUrl)) {
    return new URL(restUrl).origin;
  }

  try {
    const href = window.top?.location?.href;
    if (href && /^https?:\/\//i.test(href)) {
      return new URL(href).origin;
    }
  } catch (e) {}

  return "https://outlook.office.com"; // safe commercial fallback
}


function openPrefilledComposeSmart() {
  const to = "emergencynews@aresmgmt-dev.com";
  const subject = "Emergency News!";
  const body = "Hi,\n\nThere's something in...";

  const hostName = Office.context?.mailbox?.diagnostics?.hostName;

  // OWA â†’ stay in browser
  if (hostName === "OutlookWebApp") {
    const owaOrigin = getOwaOriginSafe();

    const url =
      `${owaOrigin}/mail/deeplink/compose` +
      `?to=${encodeURIComponent(to)}` +
      `&subject=${encodeURIComponent(subject)}` +
      `&body=${encodeURIComponent(body)}`;

    window.open(url, "_blank", "noopener,noreferrer");
    return;
  }

  // Desktop / new Outlook / mobile
  window.location.href =
    `mailto:${encodeURIComponent(to)}` +
    `?subject=${encodeURIComponent(subject)}` +
    `&body=${encodeURIComponent(body)}`;
}


Office.onReady((info) => {
  if (info.host === Office.HostType.Outlook) {
    document.getElementById("sideload-msg").style.display = "none";
    document.getElementById("app-body").style.display = "flex";
    const btn = document.getElementById("composeEmergencyNews");
    if (btn) btn.addEventListener("click", openPrefilledComposeSmart);

  }
});

export async function run() {
  /**
   * Insert your Outlook code here
   */

  const item = Office.context.mailbox.item;
  let insertAt = document.getElementById("item-subject");
  let label = document.createElement("b").appendChild(document.createTextNode("Subject: "));
  insertAt.appendChild(label);
  insertAt.appendChild(document.createElement("br"));
  insertAt.appendChild(document.createTextNode(item.subject));
  insertAt.appendChild(document.createElement("br"));
}
